import { Component, OnInit } from '@angular/core';
import { VendorService } from 'src/app/vendor.service';
import { Vendor } from '../vendor';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/app/authservice/auth.service';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.scss']
})
export class ViewContactComponent implements OnInit {

  constructor(private authService:AuthService,private _service:VendorService,private route:ActivatedRoute) { }

  ngOnInit() {

    this.getVendorById();
    this.resetform();

  }

  vId:any;
  vendor = new Vendor();


  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      //console.log(this.vendor.vendor_name);
    },(error)=>{
      console.log(error);
    })
  }

  resetform() {
    this._service.vendor = {
      vendor_id: null,
      vendor_name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      cont_id: '',
      name: '',
      dept: '',
      email: '',
      phone: '',
      isActive: null
    }
  }


  logout(){
    this.authService.logout();
  }

}
