import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './Login/login/login.component';
import { VendorComponent } from './vendor/vendor.component';
import { AddVendorComponent } from './vendor/add-vendor/add-vendor.component';
import { EditVendorComponent } from './vendor/edit-vendor/edit-vendor.component';
import { ViewContactComponent } from './vendor/view-contact/view-contact.component';
import { AuthGuard } from './guards/auth.guard';


const routes: Routes = [
  {
    path:'',
    redirectTo: 'login', pathMatch: 'full'
  },
  {
    path:'login',
    component:LoginComponent
  },
  {
    path:'vendor',
    component:VendorComponent, canActivate: [AuthGuard]
  },
  {
    path:'addVendor',
    component:AddVendorComponent, canActivate: [AuthGuard]
  },
  {
    path:'editVendor/:id',
    component:EditVendorComponent, canActivate: [AuthGuard]
  },
  {
    path:'viewcontact/:id',
    component:ViewContactComponent, canActivate: [AuthGuard]
  }


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
